import quotedPrintable from 'quoted-printable';

import utf8 from 'utf8';

import JSZip from 'jszip';

import { ChromeMessagingAction } from 'models/ChromeMessaging';
import type {
  PlatformStorage,
  PlatformStorageFlowPage,
  PlatformStoragePages,
  PlatformStorageResources,
} from 'models/ChromeStorage';
import type { Flow, FlowMap } from 'models/Flow';
import { getFromStorage, executePageFunction, getTabId, setToStorage } from 'lib/ChromeUtils';
import { Devices } from 'lib/Devices';
import type { CustomDevice, PresetDevice, PresetDeviceId } from 'models/Devices';
import { DeviceId } from 'models/Devices';
import { getPageViewport } from 'lib/Utils';

/**
 * Execute a snapshot on all frames.
 *
 * The first attempt (to me, the cleanest way) was to execute the snapshot script through chrome.scripting.executeScript on all frames,
 * and then retrieve the result directly from snapshot return value.
 * This seems not working beacause of a Chrome issue when injecting async code on allFrames @see https://github.com/GoogleChrome/chrome-extensions-samples/issues/715
 *
 * The workaround is:
 * 1. @function PageCaptureBackground.detectFrames inject a script to retrieve all frames available on a page. IMPORTANT: in this case, I'm not reading the result returned by the function,
 *    but communicating with the extension through the a sendMessage from the injected script. This workaround bypasses the issue described above.
 * 2. @function PageCaptureBackground.snapshotPage Loop throug all frameIds and execute the snapshot on each one individually. This is also a workaround beacuse of the issue above.
 */

interface PageCapturePart {
  headerMap: Record<string, string>;
  body: string;
}

interface SnapshotCsResponse {
  url: string;
  html: string;
}

interface StorageResource {
  url: string;
  content: string;
  encoding: string;
  size: number;
  contentType: string;
}

type SnapshotInjectionResult = chrome.scripting.InjectionResult<SnapshotCsResponse>;

/**
 * Class that handles the page capture process.
 * The main sources used to build the final snapshot are:
 * - chrome native MHTM snapshot for external CSS and to map the external iframes
 * - network recording for images and fonts
 * - content script snapshot.js for the HTML
 */
export class PageCaptureCore {
  /**
   * List of resources recorded by chrome network api.
   * Clears when page refreshes.
   */
  resourcesList: string[];

  /**
   * The content of the resources recorded from te network.
   * They are then stored on local storage and cleared
   * from here when a snapshot is takne.
   */
  resourcesMap: Record<string, StorageResource>;

  constructor() {
    this.resourcesList = [];
    this.resourcesMap = {};

    const resetResources = this.onNavigateResetResources.bind(this);

    chrome.webNavigation.onBeforeNavigate.addListener(resetResources);
  }

  static logStorage(): void {
    chrome.storage.local.get((response) => {
      console.log('-----------------------------------------');
      Object.entries(response).forEach(([key, value]) => {
        console.log('KEY: ', key);
        console.log(value);
      });
      console.log('-----------------------------------------');
    });
  }

  static clearStorage(): void {
    chrome.storage.local.clear();
  }

  static computeSize(body: string): number {
    const encoder = new TextEncoder();
    return encoder.encode(body).length;
  }

  static async zipFlow(flow: Flow, zipName?: string): Promise<Blob | null> {
    const zip = new JSZip();
    const promises: Promise<void>[] = [];

    let device: CustomDevice | PresetDevice | undefined = Devices.find((_device) => _device.id === flow.device);
    if (flow.device === DeviceId.CUSTOM && flow.pageViewport) {
      device = {
        id: DeviceId.CUSTOM,
        name: DeviceId.CUSTOM,
        viewport: {
          width: flow.pageViewport.innerWidth,
          height: flow.pageViewport.innerHeight,
          touch: flow.pageViewport.touch,
        },
      };
    }

    /** @TODO remove check on devie and abort the download if not found for any reason? */
    const flowMap: FlowMap = {
      name: flow.name,
      steps: [],
      device: device?.viewport
        ? { ...device?.viewport, scaleFactor: flow.scaleFactor }
        : { width: 1280, height: 1024, touch: false, scaleFactor: flow.scaleFactor },
      resourcesMap: {},
    };

    const resourcesFiles: Record<string, Record<string, string>> = {};
    let currentFile = 1;
    let fileSize = 0;
    const dioboe: string[] = [];

    flow.pages.forEach((flowPage) => {
      // Add step into flow map
      flowMap.steps.push({
        id: flowPage.id,
        name: flowPage.name,
      });

      // Create new promise to add page to zip
      promises.push(
        new Promise<void>((resolve) => {
          getFromStorage<PlatformStoragePages>([flowPage.id]).then(async (result) => {
            if (result !== undefined) {
              const page = result[flowPage.id];
              if (page) {
                zip.file(
                  `${page.id}.aqaml`,
                  JSON.stringify({
                    url: flowPage.uri,
                    snapshots: {
                      main: {
                        content: page.snapshots.main.content,
                      },
                      frames: page.snapshots.frames,
                    },
                  }),
                );

                const _result = await getFromStorage<PlatformStorageResources>(page.resources);
                if (_result !== undefined) {
                  Object.values(_result).forEach((value) => {
                    if (value?.url) {
                      const resourceSize = PageCaptureCore.computeSize(value?.content);

                      if (!dioboe.includes(value.url)) {
                        if ((fileSize + resourceSize) / (1024 * 1024) > 10) {
                          currentFile += 1;
                          fileSize = resourceSize;
                        } else {
                          fileSize += resourceSize;
                          dioboe.push(value?.url);
                        }
                      }
                      const currentFileName = `resources.${currentFile}.json`;

                      if (!resourcesFiles[currentFileName]) {
                        resourcesFiles[currentFileName] = {};
                      }
                      flowMap.resourcesMap[value?.url] = {
                        location: currentFileName,
                        mimeType: value.mimeType,
                        encoding: value.encoding,
                        contentType: value.contentType,
                      };
                      resourcesFiles[currentFileName][value?.url] = value?.content;
                    }
                  });
                }
              }
              resolve();
            }
          });
        }),
      );
    });

    await Promise.all(promises);

    Object.entries(resourcesFiles).forEach(([fileName, _resourceFile]) => {
      zip.file(fileName, JSON.stringify(_resourceFile));
    });
    zip.file(`${flow.name.toLowerCase().replaceAll(' ', '_')}.aqaflow`, JSON.stringify(flowMap));

    const content = await zip.generateAsync({ type: 'blob' });

    const url = await new Promise<string>((resolve) => {
      const reader = new FileReader();
      reader.onload = (): void => {
        const buffer = reader.result;
        if (buffer instanceof ArrayBuffer) {
          const blobUrl = `data:${content.type};base64,${btoa(
            new Uint8Array(buffer).reduce((data, byte) => data + String.fromCharCode(byte), ''),
          )}`;
          resolve(blobUrl);
        }
        resolve('');
      };
      reader.readAsArrayBuffer(content);
    });
    if (zipName) {
      chrome.downloads.download({
        url,
        filename: zipName,
        conflictAction: 'overwrite',
      });
    }
    return content;
  }

  static defaultFlowName(): string {
    const date = new Date();

    return `Recorded_${String(
      date.getMonth(),
    )}-${date.getDay()}-${date.getFullYear()}_${date.getHours()}-${date.getMinutes()}`;
  }

  static async addFlowCollection(flowName: string, device: DeviceId.CUSTOM | PresetDeviceId): Promise<Flow | null> {
    const { flowCollection } = await getFromStorage<PlatformStorage>(['flowCollection']);
    if (flowCollection?.find((flow) => flow.name === flowName)) return null;
    if (flowName.trim() === '') return null;
    const updatedFlowCollection: PlatformStorage['flowCollection'] = flowCollection ?? [];
    const tabId = await getTabId();

    const scaleFactor = await executePageFunction<number>(
      () => {
        return window.devicePixelRatio;
      },
      [],
      tabId,
    );

    const pageViewport = await getPageViewport();

    const newFlow = {
      name: flowName,
      date: new Date().getTime(),
      pages: [],
      device,
      pageViewport,
      scaleFactor: scaleFactor ?? 1,
    };

    updatedFlowCollection.push(newFlow);
    await setToStorage({ flowCollection: updatedFlowCollection });
    return newFlow;
  }

  static async updateFlowCollection(updatedFlow: Flow): Promise<PlatformStorage['flowCollection']> {
    const { flowCollection } = await getFromStorage<PlatformStorage>(['flowCollection']);

    const updatedFlowCollection = flowCollection?.map((flow) => {
      if (flow.date === updatedFlow.date) return updatedFlow;
      return flow;
    });
    await setToStorage({ flowCollection: updatedFlowCollection });
    return updatedFlowCollection;
  }

  static async updateFlowPage(page: Flow['pages'][0], selectedFlow: Flow, deletePage?: boolean): Promise<void> {
    if (deletePage) {
      selectedFlow.pages = selectedFlow.pages.filter((_page) => {
        if (_page.id === page.id) return false;
        return true;
      });
      await setToStorage({ [page.id]: undefined });
    } else {
      selectedFlow.pages = selectedFlow.pages.map((_page) => {
        if (_page.id === page.id) return page;
        return _page;
      });
    }
    await PageCaptureCore.updateFlowCollection(selectedFlow);
  }

  private static async detectFrames(pageId: string): Promise<(number | undefined)[]> {
    const tabId = await getTabId();

    return new Promise((resolveMain) => {
      if (tabId) {
        const frames: (number | undefined)[] = [];
        // eslint-disable-next-line no-inner-declarations
        function listener(
          message: { action: ChromeMessagingAction },
          sender: chrome.runtime.MessageSender,
          sendResponse: (res: unknown) => void,
        ): void {
          if (message.action === ChromeMessagingAction.ADD_FRAME) {
            frames.push(sender.frameId);
            sendResponse(`${pageId}-${sender.frameId ?? ''}`);
          }
        }

        chrome.runtime.onMessage.addListener(listener);

        chrome.scripting.executeScript({
          target: {
            tabId,
            allFrames: true,
          },
          files: ['contentscripts/iframes.bundle.js'],
        });

        /**
         * Wait that all iframes to send a message to the extension about their existance.
         * Timeout set to 1000ms - to review a way to improve this and avoid useless waiting
         * Lowed to 100ms during selenium plugin develop
         */
        setTimeout(() => {
          chrome.runtime.onMessage.removeListener(listener);
          resolveMain(frames);
        }, 100);
      }
    });
  }

  private static async snapshotPageIframes(iframes: (number | undefined)[]): Promise<SnapshotInjectionResult[]> {
    const tabId = await getTabId();

    return new Promise((resolveMain) => {
      if (tabId) {
        const executeScriptPromises: Promise<SnapshotInjectionResult | null>[] = [];

        iframes.forEach((frameId) => {
          if (frameId !== undefined) {
            executeScriptPromises.push(
              new Promise<SnapshotInjectionResult | null>((resolve) => {
                chrome.scripting
                  .executeScript<[], SnapshotCsResponse>({
                    target: {
                      tabId,
                      frameIds: [frameId],
                    },
                    files: ['contentscripts/snapshot.js'],
                  })
                  .then((injectionResults) => {
                    const [injectionResult] = injectionResults;
                    resolve(injectionResult);
                  })
                  .catch(() => {
                    resolve(null);
                  });
              }),
            );
          }
        });
        Promise.all(executeScriptPromises).then((snapshots) => {
          resolveMain(snapshots.filter((s) => s !== null) as SnapshotInjectionResult[]);
        });
      }
    });
  }

  private static parseMhtml(mhtmlText: string): PageCapturePart[] {
    const separator = '\r\n------MultipartBoundary--';
    const parts = mhtmlText.split(separator);

    // Remove the first part beacuse it is the intestation
    parts.shift();

    const parsedParts: PageCapturePart[] = [];
    parts.forEach((part) => {
      // Find the intestation separation index
      const headerEndIndex = part.indexOf('\r\n\r\n');

      if (headerEndIndex !== -1) {
        const header = part.substring(0, headerEndIndex);
        const [, headerContent] = header.split('----\r\n');
        const headerMap: PageCapturePart['headerMap'] = {};
        headerContent.split('\r\n').forEach((headerPart) => {
          const splitHeaderPart = headerPart.split(': ');
          headerMap[splitHeaderPart[0]] = splitHeaderPart[1];
        });

        const body = part.substring(headerEndIndex + 4); // add 4 to include the new line

        parsedParts.push({
          headerMap,
          body,
        });
      }
    });
    return parsedParts;
  }

  private static generateResourceId(): string {
    return new Date().getTime().toString(16) + Math.random().toString(36).substr(2);
  }

  async removeResources(resources: string[]): Promise<void> {
    await this.deleteResources(resources);
  }

  async deleteFlowCollection(flowToDelete: Flow): Promise<PlatformStorage['flowCollection']> {
    const { flowCollection } = await getFromStorage<PlatformStorage>(['flowCollection']);

    const pagesIds = flowToDelete.pages.map((page) => page.id);
    const pages = await getFromStorage<PlatformStoragePages>(pagesIds);
    if (pages) {
      const resourcesToDelete: string[] = [];
      Object.values(pages).forEach((page) => {
        if (page) {
          resourcesToDelete.push(...page.resources);
        }
      });
      await this.removeResources(resourcesToDelete);
      await chrome.storage.local.remove(pagesIds);
    }

    if (flowCollection) {
      flowCollection.splice(
        flowCollection.findIndex((_flow) => _flow.date === flowToDelete.date),
        1,
      );
      await setToStorage({ flowCollection: [...flowCollection] });
    }
    return flowCollection;
  }

  async deleteFlowPage(page: Flow['pages'][0], selectedFlow: Flow): Promise<void> {
    if (selectedFlow) {
      selectedFlow.pages = selectedFlow.pages.filter((_page) => {
        if (_page.id === page.id) return false;
        return true;
      });

      const deletedPageResponse = await getFromStorage<PlatformStoragePages>([page.id]);
      const deletedPage = deletedPageResponse[page.id];
      if (deletedPage) {
        await this.removeResources(deletedPage.resources);
      }

      await chrome.storage.local.remove(page.id);

      await PageCaptureCore.updateFlowCollection(selectedFlow);
    }
  }

  async capturePage(pageName?: string, previewImage?: string): Promise<Flow['pages'][0] | null> {
    const tabId = await getTabId();

    if (!tabId) return null;

    const date = new Date().getTime();
    const pageId = `page-${date}`;
    const mapSizes: Record<string, number> = {};

    /**
     * Genereate preview image
     */
    // const previewImage = await new Promise<string>((resolve) => {
    //   chrome.tabs.captureVisibleTab({ quality: 10 }, (dataUrl) => {
    //     resolve(dataUrl);
    //   });
    // });

    /**
     * Get the iframes list
     */
    const frames = await PageCaptureCore.detectFrames(pageId);

    /**
     * Generate MHTML and assemble
     */
    const flowPage = await new Promise<Flow['pages'][0]>((resolve) => {
      chrome.pageCapture.saveAsMHTML({ tabId }, async (mhtmlData): Promise<void> => {
        const mhtmlText = await mhtmlData?.text();
        if (mhtmlText === undefined) return;

        /** Parse the MTHML */
        const parsedMhtml = PageCaptureCore.parseMhtml(mhtmlText);

        /**
         * --- SET FRAMENAME PROCESS ---
         * This workaround allow to associate the frame name between snapshot and iframe element
         * for 3rd party iframes where window.contentFrame is null.
         *
         * Filter only HTML resources and decode quoted-printable body
         */
        const parsedMhtmlFrames = parsedMhtml
          .filter((parsed) => parsed.headerMap['Content-Type'] === 'text/html')
          .map((parsed) => {
            try {
              return {
                ...parsed,
                body: utf8.decode(quotedPrintable.decode(parsed.body)),
              };
            } catch (e) {
              return {
                ...parsed,
                body: quotedPrintable.decode(parsed.body),
              };
            }
          });

        /** Get the un-framename from iframe HTML */
        const tempIdMap: Record<string, string> = {};
        parsedMhtmlFrames.forEach((parsed) => {
          const { body } = parsed;
          if (body.includes('data-un-htmlframename="')) {
            const [, frameNamePartial] = body.split('data-un-htmlframename="');
            const [frameName] = frameNamePartial.split('"');
            let cid = parsed.headerMap['Content-ID'];
            cid = cid.split('frame-')[1];
            cid = cid.split('@')[0];
            parsedMhtmlFrames.some((mhtmlFrame) => {
              const mhtmlFrameBody = mhtmlFrame.body;
              if (
                parsed.headerMap['Content-ID'] !== mhtmlFrame.headerMap['Content-ID'] &&
                mhtmlFrameBody.includes(cid)
              ) {
                try {
                  const [bodyBeforeCid] = mhtmlFrameBody.split(cid);
                  const lastIframeIndex = bodyBeforeCid.lastIndexOf('iframe');
                  const iframeTagElementPartial = mhtmlFrameBody.substring(lastIframeIndex, mhtmlFrameBody.length);
                  const [iframeTagElement] = iframeTagElementPartial.split('>');
                  const [, frameIdPartial] = iframeTagElement.split('data-un-frameid="');
                  const [frameId] = frameIdPartial.split('"');
                  tempIdMap[frameId] = frameName;
                  return true;
                } catch (e) {
                  return false;
                }
              }
              return false;
            });
          }
        });

        /** Set the un-framename to the iframe elements*/
        await new Promise<void>((resolveMain) => {
          chrome.scripting
            .executeScript({
              target: {
                tabId,
                frameIds: [0, ...frames] as number[], // removed allFrames because of Chrome not resolving the promise
              },
              func(_tempIdMap) {
                const _frames = document.getElementsByTagName('iframe');
                for (let i = 0; i < _frames.length; i += 1) {
                  const frame = _frames[i];
                  const tempId = frame.getAttribute('data-un-frameid');
                  if (tempId && _tempIdMap[tempId]) {
                    frame.setAttribute('un-framename', _tempIdMap[tempId]);
                  }
                }
                return true;
              },
              args: [tempIdMap],
            })
            .finally(() => {
              resolveMain();
            });
        });
        /** --- END SET FRAMENAME PROCESS --- */

        /** Get the external CSS resources */
        const cssResources = parsedMhtml.filter((resource) => {
          return (
            resource.headerMap['Content-Type'] === 'text/css' &&
            !resource.headerMap['Content-Location'].startsWith('cid:')
          );
        });

        const storagePromises: Promise<void>[] = [];
        cssResources.forEach((resource) => {
          const sr: StorageResource = {
            url: resource.headerMap['Content-Location'],
            content: utf8.decode(quotedPrintable.decode(resource.body)),
            encoding: 'utf-8',
            size: PageCaptureCore.computeSize(resource.body),
            contentType: resource.headerMap['Content-Type'],
          };
          const resourceId = PageCaptureCore.generateResourceId();
          this.resourcesList.push(resourceId);
          mapSizes[sr.url] = sr.size;
          storagePromises.push(
            new Promise((_resolve) => {
              chrome.storage.local.set({ [resourceId]: sr }).then(_resolve);
            }),
          );
        });
        await Promise.all(storagePromises);

        /** Generate iframes AQA main page snapshot */
        const [mainSnapshot] = await chrome.scripting.executeScript<[], SnapshotCsResponse>({
          target: {
            tabId,
          },
          files: ['contentscripts/snapshot.js'],
        });

        /** Generate iframes AQA snapshot */
        const snapshots = await PageCaptureCore.snapshotPageIframes(frames);

        /** Assemble the page snapshot object */
        const snapshotsObj: PlatformStorageFlowPage['snapshots'] = {
          main: {
            content: mainSnapshot.result.html,
            url: mainSnapshot.result.url,
          },
          frames: snapshots.map((snapshot) => {
            mapSizes[`${pageId}-${snapshot.frameId}`] = PageCaptureCore.computeSize(snapshot.result.html);

            return {
              id: `${pageId}-${snapshot.frameId}`,
              content: snapshot.result.html,
              url: snapshot.result.url,
            };
          }),
        };
        mapSizes[`${pageId}-main`] = PageCaptureCore.computeSize(snapshotsObj.main.content);

        /** Save the page snapshot and network resources on storage */
        const storageFlowPage: PlatformStorageFlowPage = {
          id: pageId,
          resources: [...this.resourcesList],
          snapshots: snapshotsObj,
        };
        // this.resourcesList = [];

        await chrome.storage.local.set({
          [pageId]: storageFlowPage,
        });

        const storeResourcesPromises = Object.entries(this.resourcesMap).map(([resourceKey, resourceValue]) => {
          return new Promise<void>((_resolve) => {
            mapSizes[resourceValue.url] = resourceValue.size;
            chrome.storage.local.set(
              {
                [resourceKey]: resourceValue,
              },
              _resolve,
            );
          });
        });
        await Promise.all(storeResourcesPromises);
        this.resourcesMap = {};

        /** Get the page title */
        const [pageTitle] = await chrome.scripting.executeScript<[], string>({
          target: {
            tabId,
          },
          func: () => {
            return document.title || 'unset';
          },
        });

        /** Get the page uri */
        const pageUri = await executePageFunction<{ uri: string; width: number; height: number }>(
          function getPageUri() {
            return { uri: window.location.href, width: window.innerWidth, height: window.innerHeight };
          },
          [],
          tabId,
        );

        let name = pageName ?? (typeof pageTitle.result === 'string' ? pageTitle.result : 'unset');
        if (name.length > 255) {
          name = name.substring(0, 255);
        }

        resolve({
          id: pageId,
          uri: pageUri?.uri ?? 'unable to read uri',
          width: pageUri?.width ?? 0,
          height: pageUri?.height ?? 0,
          name,
          mapSizes,
          preview: previewImage ?? '',
        });
      });
    });

    return flowPage;
  }

  deleteResources(toDelete: string[]): Promise<void> {
    const _toDelete = toDelete.filter((resource) => !this.resourcesList.includes(resource));
    return new Promise<void>((resolve) => {
      chrome.storage.local.remove(_toDelete, resolve);
    });
  }

  onNavigateResetResources(details: { frameId: number }): void {
    if (details.frameId === 0) {
      this.resourcesList = [];
      this.resourcesMap = {};
    }
  }
}
