import type { ChromeMessagePayload } from 'models/ChromeMessaging';
import type { PlatformStorage, PlatformStoragePages } from 'models/ChromeStorage';

export const EXT_CONNECTION_PREFIX = 'aqa-pagecapture-';

// Chrome manifest V3 is still under development, types not updated
interface ExecuteScriptObj {
  target: { tabId: number };
  func: () => unknown;
  world: 'ISOLATED' | 'MAIN';
}

export function sendMessage<ResponseType>(
  payload: ChromeMessagePayload,
  callback?: (response: ResponseType) => void,
): void {
  const { tabId } = chrome.devtools.inspectedWindow;

  chrome.tabs.sendMessage(
    tabId,
    {
      ...payload,
    },
    callback ?? ((): void => {}),
  );
}

export function executePageFunction<T>(func: () => unknown, args?: never[], _tabId?: number): Promise<T | null> {
  const tabId = _tabId ?? chrome.devtools.inspectedWindow.tabId;

  return new Promise((resolve) => {
    chrome.scripting.executeScript(
      {
        target: { tabId },
        func,
        args,
        world: 'MAIN',
      } as ExecuteScriptObj,
      (response) => {
        const e = chrome.runtime.lastError;
        if (e === undefined && response && response.length) {
          const _response = response[0].result as T | null | undefined;
          if (_response) {
            resolve(_response);
          } else {
            resolve(null);
          }
        } else {
          resolve(null);
        }
      },
    );
  });
}

export function getFromStorage<T>(elements: string[]): Promise<Partial<T>> {
  return new Promise((resolve) => {
    chrome.storage.local.get(elements, (response) => {
      resolve((response || {}) as Partial<T>);
    });
  });
}

export function getPageFromStorage<T, Key extends keyof T>(elements: Key[]): Promise<Partial<Pick<T, Key>>> {
  return new Promise((resolve) => {
    chrome.storage.local.get(elements, (response) => {
      resolve((response || {}) as Partial<Pick<T, Key>>);
    });
  });
}

export function getNetworkRequestFromStorage<Key extends keyof PlatformStoragePages>(
  elements: Key[],
): Promise<Partial<Pick<PlatformStoragePages, Key>>> {
  return new Promise((resolve) => {
    chrome.storage.local.get(elements, (response) => {
      resolve((response || {}) as Partial<Pick<PlatformStoragePages, Key>>);
    });
  });
}

/**
 * Store user data to extension storage.
 *
 * @see https://developer.chrome.com/extensions/storage
 */
export function setToStorage(elements: Partial<PlatformStorage>): Promise<void> {
  return new Promise((resolve) => {
    chrome.storage.local.set(elements, resolve);
  });
}

/**
 * Check if dark mode enable
 */
export function isDarkMode(): boolean {
  if (chrome.devtools) {
    return chrome.devtools.panels.themeName === 'dark';
  }
  if (window?.matchMedia('(prefers-color-scheme: dark)')) {
    return window?.matchMedia('(prefers-color-scheme: dark)').matches;
  }
  return false;
}

/**
 * Send messages to background page.
 */
export function sendBackgroundMessage<ResponseType>(message: ChromeMessagePayload): Promise<ResponseType> {
  return new Promise((resolve) => {
    chrome.runtime.sendMessage(message, (response: ResponseType) => {
      resolve(response);
    });
  });
}

export async function getTabId(): Promise<number | undefined> {
  if (chrome.devtools) {
    return chrome.devtools.inspectedWindow.tabId;
  }

  const [tab] = await chrome.tabs.query({ active: true, currentWindow: true });
  return tab.id;
}

/**
 * Attempts to connect listeners within the extension background page.
 */
export async function connectToBackground(): Promise<void> {
  const tabID = await getTabId();
  if (tabID) {
    chrome.runtime.connect({
      name: `${EXT_CONNECTION_PREFIX}${tabID}`,
    });
  }
}
