import type { CustomDevice, PresetDevice } from './Devices';

export const enum ChromeMessagingAction {
  ADD_FRAME = 'new-frame',
  DO_SNAPSHOT = 'do-snapshot',
  GET_SIDEPANEL_WIDTH = 'get-sidepanel-width',
  LOADER_ON = 'loader-on',
  LOADER_OFF = 'loader-off',
  RELOAD_FLOW_COLLECTION = 'reload_flow_collection',
  RESIZE_WINDOW = 'resize-window',
}

export interface ChromeMsgResizeWindow {
  action: ChromeMessagingAction.RESIZE_WINDOW;
  device: CustomDevice | PresetDevice;
}

export type ChromeMessagePayload = ChromeMsgResizeWindow;
