import { PageCaptureBackground } from 'lib/PageCapture/PageCaptureBackground';
import type { ChromeExternalMessagePayload, ChromeExtResponse } from 'models/ChromeExternalMessaging';
import { ChromeExternalMessagingAction } from 'models/ChromeExternalMessaging';
import { DeviceId } from 'models/Devices';

const pageCapture = new PageCaptureBackground();

let selectedFlow: number | null = null;

chrome.runtime.onMessageExternal.addListener(
  (message: ChromeExternalMessagePayload, sender, sendResponse: (response: ChromeExtResponse) => void) => {
    /**
     * CREATE FLOW
     */
    if (message.action === ChromeExternalMessagingAction.CREATE_FLOW) {
      const {
        payload: { device, name },
      } = message;
      PageCaptureBackground.addFlowCollection(
        name ?? `${PageCaptureBackground.defaultFlowName()}`,
        device ?? DeviceId.CUSTOM,
      ).then((response) => {
        if (response) {
          selectedFlow = response.date;
          sendResponse({ success: true });
        } else {
          selectedFlow = null;
          sendResponse({ success: false, error: 'unknown' });
        }
      });

      return true;
    }

    /**
     * DO SNAPSHOT
     */
    if (message.action === ChromeExternalMessagingAction.DO_SNAPSHOT) {
      const {
        payload: { pageName },
      } = message;
      if (selectedFlow) {
        pageCapture.doSnapshot(selectedFlow, pageName).then((response) => {
          sendResponse(response);
        });
        return true;
      }
      sendResponse({ success: false, error: 'no selected flow' });
      return false;
    }

    /**
     * UPLOAD ZIP
     */
    if (message.action === ChromeExternalMessagingAction.UPLOAD) {
      if (selectedFlow) {
        PageCaptureBackground.uploadFlow({ flow: selectedFlow, ...message.payload }).then((response) => {
          if (response) {
            sendResponse(response);
          } else {
            sendResponse({ success: false, error: 'unable to upload flow' });
          }
        });

        return true;
      }
      sendResponse({ success: false, error: 'no selected flow' });
      return false;
    }

    /**
     * DOWNLOAD ZIP
     */
    if (message.action === ChromeExternalMessagingAction.DOWNLOAD) {
      if (selectedFlow) {
        PageCaptureBackground.downloadFlow({ flow: selectedFlow }).then(() => {
          sendResponse({ success: true });
        });

        return true;
      }
      sendResponse({ success: false, error: 'unable to upload flow' });
      return false;
    }

    /**
     * UTILITIES
     */
    if (message.action === ChromeExternalMessagingAction.NETWORK_ADD_RESOURCE) {
      pageCapture.addNetworkResource(message.resource);
      sendResponse({ success: true });
      return false;
    }

    if (message.action === ChromeExternalMessagingAction.HEALTH_CHECK) {
      sendResponse({ success: true });
      return false;
    }
    return false;
  },
);
