import { getFromStorage, executePageFunction, getTabId } from 'lib/ChromeUtils';
import { getDevice } from 'lib/Devices';
import { getPageViewport, includeDevtoolsPanel } from 'lib/Utils';
import type { PlatformStorage } from 'models/ChromeStorage';
import type { Flow } from 'models/Flow';

import type { ChromeExtResponse } from 'models/ChromeExternalMessaging';

import type { CustomDevice, PresetDevice } from 'models/Devices';
import { DeviceId } from 'models/Devices';

import { PageCaptureCore } from './PageCaptureCore';

interface StorageResource {
  url: string;
  content: string;
  encoding: string;
  size: number;
  contentType: string;
}

/**
 * Class that handles the page capture process.
 * The main sources used to build the final snapshot are:
 * - chrome native MHTM snapshot for external CSS and to map the external iframes
 * - network recording for images and fonts
 * - content script snapshot.js for the HTML
 */
export class PageCaptureBackground extends PageCaptureCore {
  /**
   * NOT WORKING - REST api - lunedi
   */
  static async uploadFlow(options: {
    flow: number;
    name: string;
    suite: string;
    team: string;
    xTeam: string;
    description?: string;
    testId?: string;
  }): Promise<{ success: false; error: string } | { success: true }> {
    const defaultErroResponse = { success: false, error: 'unable to create flow' };
    const { flow, name, description, team, suite, xTeam, testId } = options;
    const { flowCollection } = await getFromStorage<PlatformStorage>(['flowCollection']);
    const selectedFlow = flowCollection?.find((_flow) => _flow.date === flow);
    if (selectedFlow) {
      const zip = await PageCaptureBackground.zipFlow(selectedFlow);
      if (zip) {
        return new Promise<{ success: false; error: string } | { success: true }>((resolve) => {
          const formData = new FormData();
          formData.append('name', name);
          formData.append('description', description ?? '');
          formData.append('pagecapture', zip, 'pagecapture.zip');
          const apiEnv = apiDomain ?? 'api-aqa.usablenet.com';
          if (testId) {
            fetch(`https://${apiEnv}/v3/${team}/a11y/suites/${suite}/flows/${testId}/update`, {
              method: 'POST',
              body: formData,
              headers: {
                'X-Team': xTeam,
              },
            })
              .then((response) => response.json())
              .catch(() => {
                resolve(defaultErroResponse);
              })
              .then((response: { id: string; time: number } | { statusCode: number; error?: 'part_not_found' }) => {
                if ('error' in response) {
                  if (response.error === 'part_not_found') {
                    resolve({
                      success: false,
                      error: 'test not found',
                    });
                  } else {
                    resolve(defaultErroResponse);
                  }
                } else {
                  resolve({ success: true });
                }
              })
              .catch(() => {
                resolve(defaultErroResponse);
              });
          } else {
            fetch(`https://${apiEnv}/v3/${team}/a11y/suites/${suite}/flows/pagecapture/create`, {
              method: 'POST',
              body: formData,
              headers: {
                'X-Team': xTeam,
              },
            })
              .then((response) => response.json())
              .catch(() => {
                resolve(defaultErroResponse);
              })
              .then((response: { id: string; time: number } | { statusCode: number; error?: 'existing_name' }) => {
                if ('error' in response) {
                  if (response.error === 'existing_name') {
                    resolve({
                      success: false,
                      error: 'flow name already exists',
                    });
                  } else {
                    resolve(defaultErroResponse);
                  }
                } else {
                  resolve({ success: true });
                }
              })
              .catch(() => {
                resolve(defaultErroResponse);
              });
          }
        });
      }
    }
    return defaultErroResponse;
  }

  static async downloadFlow(options: { flow: number }): Promise<boolean> {
    const { flow } = options;
    const { flowCollection } = await getFromStorage<PlatformStorage>(['flowCollection']);
    const selectedFlow = flowCollection?.find((_flow) => _flow.date === flow);
    if (selectedFlow) {
      const zip = await PageCaptureBackground.zipFlow(selectedFlow, `${selectedFlow.name}.zip`);
      if (zip) {
        return true;
      }
    }
    return false;
  }

  addNetworkResource(resourceToAdd: StorageResource): void {
    this.resourcesMap[resourceToAdd.url] = resourceToAdd;
    this.resourcesList.push(resourceToAdd.url);
  }

  async doSnapshot(selectedFlow: Flow | number, pageName?: string): Promise<ChromeExtResponse> {
    const tabId = await getTabId();
    if (!tabId) return { success: false, error: 'no tab selected' };

    let _selectedFlow = null;

    if (typeof selectedFlow === 'number') {
      const { flowCollection } = await getFromStorage<PlatformStorage>(['flowCollection']);
      _selectedFlow = flowCollection?.find((_flow) => _flow.date === selectedFlow);
    } else {
      _selectedFlow = selectedFlow;
    }
    if (_selectedFlow) {
      const scaleFactor = await executePageFunction<number>(
        () => {
          return window.devicePixelRatio;
        },
        [],
        tabId,
      );

      if (scaleFactor !== _selectedFlow.scaleFactor) {
        return {
          success: false,
          error:
            'Error: monitor pixel ratio changed. Please create a new flow or use the monitor where the flow has been created.',
        };
      }

      let device: CustomDevice | PresetDevice | null = null;
      if (_selectedFlow.device === DeviceId.CUSTOM) {
        if (_selectedFlow.pageViewport) {
          const currentViewport = await getPageViewport();
          if (
            currentViewport?.innerWidth !== _selectedFlow.pageViewport.innerWidth ||
            currentViewport?.innerHeight !== _selectedFlow.pageViewport.innerHeight
          ) {
            return {
              success: false,
              error: 'viewport is changed, please restore the initial viewport or create a new flow',
            };
          }
          device = {
            id: DeviceId.CUSTOM,
            name: DeviceId.CUSTOM,
            viewport: {
              width: _selectedFlow.pageViewport.innerWidth,
              height: _selectedFlow.pageViewport.innerHeight,
              touch: _selectedFlow.pageViewport.touch,
            },
          };
        }
      } else {
        device = getDevice(_selectedFlow.device);
      }
      if (device && _selectedFlow.device !== DeviceId.CUSTOM) {
        const adjustedViewport = await includeDevtoolsPanel(device.viewport);
        await new Promise<void>((resolve) => {
          chrome.windows.getCurrent((window) => {
            if (window.id !== undefined) {
              const updateInfo: chrome.windows.UpdateInfo = {
                width: adjustedViewport.width,
                height: adjustedViewport.height,
                state: 'normal',
              };
              chrome.windows.update(window.id, updateInfo).then(() => {
                resolve();
              });
            }
          });
        });
      }

      const capturedPage = await this.capturePage(pageName);
      if (capturedPage) {
        _selectedFlow.pages.push(capturedPage);
        await PageCaptureBackground.updateFlowCollection(_selectedFlow);
        return { success: true };
      }
    }
    return { success: false, error: 'no selected flow' };
  }
}
