export const enum DeviceId {
  DESKTOP_1 = 'device_0',
  DESKTOP_2 = 'device_1',
  DESKTOP_3 = 'device_2',
  MOBILE_1 = 'device_3',
  CUSTOM = 'custom',
}

export type PresetDeviceId = DeviceId.DESKTOP_1 | DeviceId.DESKTOP_2 | DeviceId.DESKTOP_3 | DeviceId.MOBILE_1;

export interface DeviceViewport {
  width: number;
  height: number;
  touch: boolean;
}

export interface DeviceGeneric {
  name: string;
  viewport: DeviceViewport;
}

export interface PresetDevice extends DeviceGeneric {
  id: PresetDeviceId;
}

export interface CustomDevice extends DeviceGeneric {
  id: DeviceId.CUSTOM;
}
export type Devices = PresetDevice[];
