import type { PresetDevice } from 'models/Devices';

import { executePageFunction, getTabId } from './ChromeUtils';

export interface PageVieport {
  outerWidth: number;
  outerHeight: number;
  innerWidth: number;
  innerHeight: number;
  touch: boolean;
}

export function addNodesIdentifier(): void {
  let i = 0;
  const getChildren = (elm: Element | Node): (Element | Node)[] => {
    const children: (Element | Node)[] = Array.from(elm.childNodes || []);
    if ('shadowRoot' in elm && elm.shadowRoot) {
      children.unshift(elm.shadowRoot);
    }
    return children;
  };

  const walkThroughChildren = (elm: Element | Node): void => {
    if ('setAttribute' in elm) {
      elm.setAttribute('un-id', i.toString());
      i += 1;
    }
    const children = getChildren(elm);
    children.forEach((child) => {
      walkThroughChildren(child);
    });
  };
  walkThroughChildren(document.documentElement);
}

/**
 * This function compute the correct size of the desidere viewport adding the width of the Chrome side panel.
 */
export async function includeDevtoolsPanel(viewport: PresetDevice['viewport']): Promise<PresetDevice['viewport']> {
  const tabId = await getTabId();
  if (!tabId) return viewport;

  const windowWidth = await executePageFunction<{ innerWidth: number; outerWidth: number }>(
    function getPageWindowSize() {
      return { innerWidth: window.innerWidth, outerWidth: window.outerWidth };
    },
    [],
    tabId,
  );

  const sidePanelPadding = windowWidth ? windowWidth.outerWidth - windowWidth.innerWidth : 0;
  return {
    width: viewport.width + sidePanelPadding,
    height: viewport.height,
    touch: viewport.touch,
  };
}

export async function getPageViewport(): Promise<PageVieport | null> {
  const tabId = await getTabId();

  if (tabId) {
    const windowViewport = await executePageFunction<PageVieport>(
      function getPageWindowSize() {
        const isTouch = 'ontouchstart' in window || navigator.maxTouchPoints > 0;
        return {
          outerWidth: window.outerWidth,
          outerHeight: window.outerHeight,
          innerWidth: window.innerWidth,
          innerHeight: window.innerHeight,
          touch: isTouch,
        };
      },
      [],
      tabId,
    );

    return windowViewport;
  }
  return null;
}
